import React, { createContext, useState } from 'react'

import { Billet } from '@/core/types/Billet'

interface AppContextInterface {
  isLoggedIn: boolean
  login: () => void
  logout: () => void

  billets: Billet[]
  setBillets: (billets: Billet[]) => void
}

export const AppContext = createContext<AppContextInterface>(
  {} as AppContextInterface
)

export const AppProvider: React.FC = ({ children }) => {
  const [isLoggedIn, setLogged] = useState(false)

  function login() {
    setLogged(true)
  }

  function logout() {
    setLogged(false)
  }

  const [billets, setBilletsList] = useState<Billet[]>([])

  function setBillets(billets: Billet[]) {
    setBilletsList(billets)
  }

  return (
    <AppContext.Provider
      value={{ isLoggedIn, login, logout, billets, setBillets }}
    >
      {children}
    </AppContext.Provider>
  )
}
